<template>
	<!-- <div class="pc_content"> -->
		<div class="listBoss">
		<van-list
		  v-model="loading"
		  :finished="finished"
		  finished-text="没有更多了"
		  @load="onLoad"
		  class="list">

		  <!-- 转化文章 -->
		  <div  ref="tempForPlate" class="temp"></div>
		  <!-- 项目 -->

		  <div class="item" v-for="(item, index) in list" :key="'ListForPlate' + item.article_id"
		   @click="articleJump(item.article_id)">
		    <!-- 上部 -->
		    <UserTag :msgg="item"></UserTag>
		    <!-- <HeadPor :msgg="{ authen:item.user_authen, img:item.user_img,name:item.user_name,user_lv:item.user_lv,gold:item.user_gold}"></HeadPor> -->
		    <!-- <HeadPor :msgg="[item.user_img,item.user_name]"></HeadPor> -->
		    <!-- 中部 -->
		    <div class="middle">
		      <!-- 标题 -->
		      <div class="title ">{{item.article_title}}</div>
		      <!-- 内容 -->
		      <!-- <div class="content van-ellipsis">{{item.article_content}}</div> -->
		      <div class="ccc">
		        <div class="content van-multi-ellipsis--l3" :ref="`des${index}`">{{item.article_content}}</div>
		        <img :src="item.hasImg.imgArr[0]" alt="" v-show="item.hasImg.hasImgONe">
		      </div>
		      <!-- 图片群 -->
		      <div class="imgs" v-show="item.hasImg.hasImgTag">
		        <img :src="item2" v-for="(item2,index2) in item.hasImg.imgArr" :key="index2"
		         class="hasImg">
		      </div>
		    </div>

		    <!-- 下部 -->
		    <div class="bottom">
		      <div>
		        <!-- 时间 -->
		        <i class="iconfont time">&#xe607;</i>
		        <span class="spanOne">{{item.article_publish_time|fTime}}</span>
		      </div>

		      <div>
		        <!-- 评论量 -->
		        <i class="iconfont comment">&#xe61d;</i>
		        <span class="spanOne">{{item.article_comment_count}}</span>
		      </div>

		      <div>
		        <!-- 浏览量 -->
		        <i class="iconfont eye">&#xe62b;</i>
		        <span class="spanOne">{{item.article_look_count}}</span>
		      </div>

		      <div>
		        <!-- 收藏量 -->
		        <i class="iconfont col">&#xe68b;</i>
		        <span>{{item.article_col_count}}</span>
		      </div>

		    </div>
		  </div>



		</van-list>
		<!-- </van-pull-refresh> -->
		</div>

<!-- 	<div class="pc_right"></div>
	</div> -->


</template>

<script>
	/* 引入头像组件*/
	// import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"

  import {createNamespacedHelpers} from 'vuex'
  const list=createNamespacedHelpers('list')
  const login=createNamespacedHelpers('login')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        // 选项框
          showSortBar : false,
          list: [],
          loading: false,
          finished: false,
          // isLoading: false,
          // value1: 'article_comment_time' ,
          // option1: [
          //   { text: '最新评论', value: 'article_comment_time' },
          //   { text: '最新文章', value: 'article_publish_time' },
          // ],
      }
    },
    filters:{
      fNewAOrC : function(value){
        //最新文章
        if(value == 'article_publish_time'){
          return '最新文章'
        }
        if(value == 'article_comment_time'){
          return '最新评论'
        }
      }
    },
    /* 方法*/
    methods:{
      // 网络加载
      // ...login.mapMutations({
      //       	loadingForHttpHandler: 'loadingForHttpHandler'
      //       }),
      // 得到文章列表
      getArticle(params,num){
        // alert(1)
        /* ***********************************************************************************/
        // this.$api.article[`${this.one[num]}`](params).then(
        this.$api.article.plateAll(params).then(
            res =>{
              // 缓冲消失
              this.isBuffering = false
              // 刷新状态结束
              this.isLoading = false
              // 加载状态结束
              this.loading = false;
              //文章触发加载(在下拉执行v-if的时候失效)
              this.finished = false
              console.log(res.data)
              if(res.data =="no_data" ){
                //文章不再触发加载
                this.finished = true
              }else if(res.data.finished){
                // alert(1)
                //如果后端告知无需刷新
                this.finished = true
                this.getFn(res.data.result,num)
              }else if(res.data.ok){
                this.getFn(res.data.result,num)
              }else{
                this.finished = true
              }
            }
        )
        /* ***********************************************************************************/
      },
      getFn(arg,num){
        for(var i =0;i<arg.length;i++){
         	// 日期处理
         	// var a = arg[i].article_publish_time.slice(0,23)
         	// var b = a.replace('T', ' ')
          // var c = new Date(b)
          //  arg[i].article_publish_time = this.formatDate(new Date(c.setHours(c.getHours() + 8)))
         	arg[i].article_publish_time = this.formatDate(new Date(arg[i].article_publish_time))
          // 这是关于内容
         	this.$refs.tempForPlate.innerHTML = arg[i].article_content
         	arg[i].article_content = this.$refs.tempForPlate.textContent
          // 内容是否图片
          arg[i].hasImg = {
           hasImgONe : false,
           hasImgTag : false,
           imgArr : []
          }
          var imgs = this.$refs.tempForPlate.getElementsByTagName("img")
          // 如果有图片
          if(!!imgs[0] == true) {
            if(imgs.length < 3){
              // 如果只有一张图片
               arg[i].hasImg.hasImgONe = true
               arg[i].hasImg.imgArr[0] = imgs[0].src
            }else{
              // 如果只有三张及以上图片
              arg[i].hasImg.hasImgTag = true
              for(let j = 0; j<3;j++){
                  arg[i].hasImg.imgArr[j] = imgs[j].src
              }
            }
          }
         }
        /* 数字*/
         switch(num){
          case 0:
              this.list = arg
              break;
          case 1:
              this.list = this.list.concat(arg)
              break;
            default:
         }
         //文章继续触发加载 因为list和listPlate 在刷新和排序的过程中有if 重新渲染 所以this.finished = false不需要
         // this.finished = false
         this.$nextTick(() => {
           for(let i=0;i<=this.list.length-1;i++){
             // 如果有一张图片
             if(!!this.list[i].hasImg.hasImgONe == true){
               // 给这个内容增加样式
               this.$refs[`des${i}`][0].classList.add('oneImg')
             }
             // 如果有三张图片
             else if(!!this.list[i].hasImg.hasImgTag == true){
               // 给这个内容增加样式
               this.$refs[`des${i}`][0].classList.add('threeImg')
             }
             else{
                 //如果没有图片
                 this.$refs[`des${i}`][0].classList.add('noImg')
              }
           }
         })
      },
      // 跳转到具体文章
        articleJump(params){
          this.$router.push({
            path: '/read',
            query : {
              articleId : params
            }
          })
        },
        // 加载
        onLoad() {
          // console.log(this.list[this.list.length-1][`${this.value1}`],'time123')
          // alert(1)
          // // 如果是搜索页
          //   if(!!this.isSearch == true){

          //   }else
            // this.skeleton
            if(!!this.list[0] == true){//more的参数
              if(!!this.one[2] == true){//if(!!this.one[3] == true){//点击细分的标签
                console.log(this.list,this.list[this.list.length-1][`${this.value1}`],'更多之前')
                var params = {
                  time : this.list[this.list.length-1][`${this.value1}`],
                  plate : this.one[1],//plate : this.one[2],
                  tag : this.one[2],//tag : this.one[3],
                  value : this.value1
                }
              }else{//点击板块全部
                var params = {
                  time : this.list[this.list.length-1][`${this.value1}`],
                  plate : this.one[1],//plate : this.one[2],
                  value : this.value1
                }
              }
               this.getArticle(params,1)

            }else{//第一次加载的参数

              if(!!this.one[2] == true){// if(!!this.one[3] == true){//点击细分标签
                console.log(this.one[3],'params')
                var params = {
                  plate : this.one[1],// plate : this.one[2],
                  tag : this.one[2],//tag : this.one[3]
                  value : this.value1
                }
                console.log(params,'params')
              }else{//点击全部
                var params = {
                  plate : this.one[1],//plate : this.one[2],
                   value : this.value1
                }
              }
               this.getArticle(params,0)

            }

      },
      formatDate(date){
        var y = date.getFullYear();
            var m = date.getMonth() + 1;
            m = m < 10 ? ('0' + m) : m;
            var d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            var h = date.getHours();
            var minute = date.getMinutes();
            minute = minute < 10 ? ('0' + minute) : minute;
            var second= date.getSeconds();
            second = second < 10 ? ('0' + second) : second;
            var Milliseconds= date.getMilliseconds();
            if(Milliseconds < 10){
              Milliseconds =  ('00' + Milliseconds)
            }
            if(Milliseconds < 100){
              Milliseconds =  ('0' + Milliseconds)
            }
            // second = second < 10 ? ('0' + second) : second;
            return y + '-' + m + '-' + d+' '+h+':'+minute+':'+ second+'.'+ Milliseconds;
      }
      // formatDate(date){
      //   var y = date.getFullYear();
      //       var m = date.getMonth() + 1;
      //       m = m < 10 ? ('0' + m) : m;
      //       var d = date.getDate();
      //       d = d < 10 ? ('0' + d) : d;
      //       var h = date.getHours();
      //       var minute = date.getMinutes();
      //       minute = minute < 10 ? ('0' + minute) : minute;
      //       var second= date.getSeconds();
      //       second = second < 10 ? ('0' + second) : second;
      //       return y + '-' + m + '-' + d+' '+h+':'+minute+':'+ second;
      // }
    },
    /* 计算*/
    computed:{
      isBuffering:{
        get () {
              return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
        },
        set (value) {
          this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
        }
      },
      value1:{
        get () {
          return this.$store._modulesNamespaceMap['list/'].context.state.value1
         },
        set (value) {
        this.$store._modulesNamespaceMap['list/'].context.commit('value1Handler', value)
        }
      },
     ...list.mapState({
          	one : state => state.one
          }),
      isLoading:{
        get () {
          return this.$store._modulesNamespaceMap['list/'].context.state.isLoadingForHomePage
        },
        set (value) {
          this.$store._modulesNamespaceMap['list/'].context.commit('isLoadingForHomePageHandler', value)
        }
      },
    // ...list.mapState({
    //      	isSearch : state => state.isSearch
    //      }),
    //      // 获取关键词
    // ...list.mapState({
    //      	KeyWord : state => state.KeyWord
    //      }),
    //      // 重置
    // ...list.mapState({
    //      	Reset : state => state.Reset
    //      }),
    //板块的下拉刷新
    // ...list.mapState({
    //      	isLoadingForPlate : state => state.isLoadingForPlate
    //      }),

    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
      // HeadPor
      UserTag

    },
    /* 挂载阶段*/
    mounted() {
      // 缓冲
      this.isBuffering = true
    },
    // 监听
    watch:{
      // 'value1': {
      //     handler(newVal, oldVal) {
      //         document.documentElement.scrollTop  = 0
      //      	  // this.finished = false
      //      	  if(!!this.one[3] == true){
      //      	    var params = {
      //      	      plate : this.one[2],
      //      	      tag : this.one[3],
      //      	      value : newVal
      //      	    }
      //      	  }else{
      //      	    var params = {
      //      	      plate : this.one[2],
      //      	      value : newVal
      //      	    }
      //      	  }
      //         this.getArticle(params,0)
      //     },
      //     // immediate: true,
      //     // deep: true
      // }
      // 'Reset': {
      //     handler(newVal, oldVal) {
      //       console.log(newVal,123)
      //       if(!!newVal == true){
      //         // 清空
      //         this.list = []
      //         // 未完成
      //         this.finished = false
      //         // 执行加载函数
      //         this.onLoad()
      //         // 重置
      //         this.ResetHandler(false)
      //       }
      //     },
      //     immediate: true,
      //     // deep: true
      // }
    }
  }
</script>



<style scoped lang="scss">
  // /* 排序栏***************************************/
