<template>
  <!-- <div class="pc_content"> -->
    <div class="plate">


        <div class="sortBoss">
          <!-- 排序栏-->
          <div :class="['sortBar', showSortBar == true ?'showSortBar':'' ]" >
              <span @click="sort('article_comment_time')" :class="[value1 == 'article_comment_time' ?'active':'']">最新评论</span>
              <span @click="sort('article_publish_time')" :class="[value1 == 'article_publish_time' ?'active':'']">最新文章</span>
          </div>
          <!-- 当前排序-->
          <div class="sortContent">
            <div class="sort" @click.stop="showSortBarHandler">
              {{value1| fNewAOrC}}
              <i class="iconfont">&#xe61a;</i>
            </div>
          </div>

        </div>



    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" success-text="刷新成功"
      class="refresh" :style = '{minHeight:winWH.height}'>

    	<div v-if="sortSwitch" class="showsortSwitch">
        <!-- 竞标栏-->
        <NoticePlate></NoticePlate>

    		<van-tabs v-model="active" @click="onClick" :border='border' sticky swipeable
    		:offset-top='0' class="tabs">

    		  <van-tab v-for="(itemm,indexx) in grid[plateIndex].tagArr"
    		  :title="grid[plateIndex].tagArr[indexx]" :key="'yyy'+ indexx">
    		    <List></List>
    		  </van-tab>

    		</van-tabs>

    	</div>

    </van-pull-refresh>
      </div>
   <!-- <div class="pc_right"></div>
  </div> -->

</template>

<script>
  import NoticePlate from "@/components/utils/NoticePlate"
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"
  /* 引入列表组件*/
  import List from "@/components/utils/ListForPlate"

  import {createNamespacedHelpers} from 'vuex'
  const gridVuex = createNamespacedHelpers('grid')
  const list=createNamespacedHelpers('list')
  const navBar_Every=createNamespacedHelpers('navBar_Every')
  // const login=createNamespacedHelpers('login')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        plateIndex : 0,
        active: 0,
        border : true,
        // 标签页距离顶部距离
        // offsettop : 40,
        //排序切换
        sortSwitch : true,
        //展示排序栏
        showSortBar : false,
        // isLoading : false
      }
    },
    filters:{
      // fb : function(value){
      //   //最新文章
      //   if(value == 'article_publish_time'){
      //     return '最新文章'
      //   }
      //   if(value == 'article_comment_time'){
      //     return '最新评论'
      //   }
      // }
    },
    /* 方法*/
    methods:{
      // 网络加载
      // ...login.mapMutations({
      //       	loadingForHttpHandler: 'loadingForHttpHandler'
      //       }),
      //展示选项框
      showSortBarHandler(){
        this.showSortBar = !this.showSortBar
      },
      //排序
      sort(arg){
        if(this.value1 == arg){
          this.showSortBar = !this.showSortBar
          return;
        }else{
          this.value1  = arg
        }
        this.showSortBar = !this.showSortBar
        this.sortSwitch = false
        this.$nextTick(() => {
          this.sortSwitch = true
        })
      },
      // 导航栏标题
      // ...navBar_Every.mapMutations({
      //       	titleHandler: 'titleHandler'
      //       }),
      ...list.mapMutations({
            	oneHandler: 'oneHandler'
            }),
      // 点击
      onClick(name, title) {//标签页点击哪一个标签
            switch(name){
              // case 0:
              //       this.oneHandler(['plateAll',
              //       'plateAllMore',
              //       `${this.grid[this.plateIndex].text}`])
              //       break;
              // default:
              //       this.oneHandler(['plateOther',
              //       'plateOtherMore',
              //       `${this.grid[this.plateIndex].text}`,
              //       `${this.grid[this.plateIndex].tagArr[name]}`])
              //       break;
              case 0:
                    this.oneHandler(['plateAll',
                    `${this.grid[this.plateIndex].text}`])//板块
                    break;
              default:
                    this.oneHandler(['plateAll',
                    `${this.grid[this.plateIndex].text}`,//板块
                    `${this.grid[this.plateIndex].tagArr[name]}`])//板块下的细分标签
                    break;
            }
      },
      onRefresh(){
        this.isLoading = true
        this.sortSwitch = false
        this.$nextTick(() => {
          this.sortSwitch = true
          // this.isLoading = false
        })
      }
    },
    //在进入每一个不同板块的时候 根据plateIndex选择不同板块
    activated(){
      switch(this.active){
        case 0:
              this.oneHandler(['plateAll',
              `${this.grid[this.plateIndex].text}`])//板块
              break;
        default:
              this.oneHandler(['plateAll',
              `${this.grid[this.plateIndex].text}`,//板块
              `${this.grid[this.plateIndex].tagArr[name]}`])//板块下的细分标签
              break;
        // case 0:
        //       this.oneHandler(['plateAll','plateAllMore',`${this.grid[this.plateIndex].text}`])
        //       break;
        // default:
        //       this.oneHandler(['plateOther','plateOtherMore',
        //       `${this.grid[this.plateIndex].text}`,
        //       `${this.grid[this.plateIndex].tagArr[this.active]}`])
        //       break;
      }
    },
    /* 计算*/
    computed:{
      value1:{
        get () {
          return this.$store._modulesNamespaceMap['list/'].context.state.value1
         },
        set (value) {
        this.$store._modulesNamespaceMap['list/'].context.commit('value1Handler', value)
        }
      },
     ...gridVuex.mapState({
          	grid : state => state.grid
          }),
      isLoading:{
        get () {
          return this.$store._modulesNamespaceMap['list/'].context.state.isLoadingForHomePage
        },
        set (value) {
          this.$store._modulesNamespaceMap['list/'].context.commit('isLoadingForHomePageHandler', value)
        }
      },
      winWH:{
        get () {
              return this.$store._modulesNamespaceMap['win/'].context.state.winWH
        },
        set (value) {
          this.$store._modulesNamespaceMap['win/'].context.commit('winWHHandler', value)
        }
      },
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
      List,
      NoticePlate
    },
    /* 挂载阶段*/
    mounted() {
      // 根据query选择板块索引
      this.plateIndex = this.$route.query.plate
      // this.titleHandler(this.grid[this.plateIndex].text)
    },
    // 监听
    watch:{
      // 'active': {
      //     handler(newVal, oldVal) {
      //       console.log(newVal)
      //       switch(newVal){
      //         case 0:
      //               this.oneHandler(['plateAll','plateAllMore',`${this.grid[this.plateIndex].text}`])
      //               break;
      //         default:
      //               this.oneHandler(['plateOther','plateOtherMore',
      //               `${this.grid[this.plateIndex].text}`,
      //               `${this.grid[this.plateIndex].tagArr[newVal]}`])
      //               break;
      //       }
      //     },
      //     immediate: true,
      //     // deep: true
      // }
    },
    beforeRouteLeave:function(to, from, next){
      /*
      缓存模块更新数据方式:
      1 if
      2 获取数据的函数 不经过 created --> mounted  等传统钩子函数
      methods 属性不经过钩子函数 所以 activated 可以更新
       (vant 组件的 下拉刷新和 list 不经过 钩子函数)
      3 暴力删除缓存 (弊端 只能删除from)
      */
       if (from && (
           /* 当前路由是/plate 并且 去的路由不是/read和/search 清除本/plate缓存
              简单点就是 /plate 去 /read和/search  不需要清除/plate缓存
           */
           (from.path=='/plate' && to.path=='/')


     ))
    	// if (from && from.meta.rank && to.meta.rank && from.meta.rank>to.meta.rank)

        {//此处判断是如果返回上一层，你可以根据自己的业务更改此处的判断逻辑，酌情决定是否摧毁本层缓存。
            if (this.$vnode && this.$vnode.data.keepAlive)
            {
                if (this.$vnode.parent && this.$vnode.parent.componentInstance && this.$vnode.parent.componentInstance.cache)
                {
                    if (this.$vnode.componentOptions)
                    {
                        var key = this.$vnode.key == null
                                    ? this.$vnode.componentOptions.Ctor.cid + (this.$vnode.componentOptions.tag ? `::${this.$vnode.componentOptions.tag}` : '')
                                    : this.$vnode.key;
                        var cache = this.$vnode.parent.componentInstance.cache;
                        var keys  = this.$vnode.parent.componentInstance.keys;
                        if (cache[key])
                        {
                            if (keys.length) {
                                var index = keys.indexOf(key);
                                if (index > -1) {
                                    keys.splice(index, 1);
                                }
                            }
                            delete cache[key];
                        }
                    }
                }
            }
            this.$destroy();
        }
        next();
    },
  }
</script>

<style scoped lang="scss">
  
  .plate{//左边
    // background-color: $jsBgcGray;
    .showsortSwitch{
      background-color: $jsBgcGray;
      .tabs{
        margin: 0 auto;
        width: 655px;
      }
    }
  
  }
  .sortBoss{
    background-color: transparent;
    position: fixed;
    // left: 130px;
    bottom : 200px;
    z-index: 99999999999999999999999999;
    /* 上面的*/
    .sortContent{
      @include flex(row,flex-end,center)
      .sort{
        color: $green;
        border-radius: 50px;
        font-size: 32px;
        font-weight: 500;
        @include wh(200px,80px);
        background-color: #DCDCDC;
        @include flex(row,center,center)
        i{
          font-size:30px
        }
      }
    }

    /* 下面的*/
    .sortBar{
      width: 700px;
      padding: 0 25px;
      height: 0;
      transition: height .1s;
      overflow: hidden;
      font-size: 40px;
      background-color: #DCDCDC;
      color:rgb(152,152,152);
      @include flex(row,space-around,center)
      span{
        flex-grow : 1;
        @include wh(146px,60px)
        @include flex(row,center,center)
      }
    }
    /* 排序兰的div 显示时候的样式*/
    .showSortBar{
      height: 100px;
    }
    /* 选中的文字样式*/
    .active{
      color:$green;
    }

  }
  /* 排序栏***************************************/
  // .am-sort{
  //   color: $green;
  //   font-size: 16px;
  //   font-weight: 500;
  //   position: fixed;
  //   top: 9px;
  //   left:15%;
  //   z-index: 99999999999999999999999999;
  //   @include flex(row,center,center)
  //   i{
  //     font-size:18px
  //   }
  // }
  // .am-sortBar{
  //   height: 0;
  //   transition: height .1s;
  //   position: fixed;
  //   top: 40px;
  //   z-index: 99999999999999;
  //   overflow: hidden;
  //   font-size: 16px;
  //   background-color: white;
  //   color:rgb(152,152,152);
  //   @include flex(row,space-around,center)
  //   span{
  //     @include wh(146px,46px)
  //     @include flex(row,center,center)
  //   }
  // }
  // .showSortBar{
  //   height: 80px;
  // }
  // .active{
  //   color:$green;
  // }
  // .sortBar{
  //   width: 700px;
  //   padding: 0 25px;
  // }
  @import "../../assets/scss/plate/plate.scss";
</style>



